.page {
  &-item {
    padding-bottom: 80px;

    @include media-breakpoint-down(md) {
      padding-bottom: 50px;
    }
  }

  &-filter {
    margin-bottom: 30px;

    @include media-breakpoint-down(md) {
      margin-bottom: 20px;
    }
  }

  &-breadcrumb {
    margin: 30px 0 35px;

    @include media-breakpoint-down(md) {
      margin: 20px 0;
    }
  }

  &-title {
    color: color(blue);
    font-weight: bold;
    margin-bottom: 20px;

    @include media-breakpoint-down(md) {
      margin-bottom: 5px;
    }
  }

  &-text {
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: #000000;

    @include fluid-type(14px, 20px);
  }

  &-head {
    margin-bottom: 35px;
  }

   .feedback-container {
      @include media-breakpoint-down(xl) {
         margin-top: 50px;
      }
   }
}

.page-houses {
  &__head {
    @include media-breakpoint-up(lg) {
      display: flex;
      justify-content: space-between;
    }
  }
}
