.question {
   background: #FFFFFF;
   box-shadow: 0 0 7px rgba(0, 0, 0, 0.25);
   border-radius: 0 7px 7px 0;
   margin-left: 25px;

   @include media-breakpoint-down(md) {
      margin-left: 15px;
   }

   &__head {
      padding: 20px 65px 20px 20px;
      position: relative;
      font-size: 22px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      cursor: pointer;

      &.active {
         svg {
            transform: none;
         }
      }

      @include fluid-type(14px, 22px);

      @include media-breakpoint-down(md) {
         padding: 10px 20px 10px 10px;
      }

      svg {
         transform: rotate(180deg);
         transition: transform .3s;
      }

      &:before {
         content: '';
         background: #43638A;
         border-radius: 7px 0px 0px 7px;
         position: absolute;
         top: 0;
         bottom: 0;
         width: 25px;
         right: 100%;

         @include media-breakpoint-down(md) {
            width: 15px;
         }
      }
   }

   &__content {
      padding: 0 20px 30px 20px;
      font-size: 20px;
      display: none;
   }

   &:not(:last-child) {
      margin-bottom: 33px;

      @include media-breakpoint-down(md) {
         margin-bottom: 10px;
      }
   }
}
