.site-header {
   background: rgba(255, 255, 255, .8);
   box-shadow: 0px -1px 10px rgba(0, 0, 0, 0.2);
   position: relative;
   z-index: 9910;
   backdrop-filter: blur(5px);

   @include media-breakpoint-down(lg) {
      &:before {
         content: '';
         bottom: 0;
         left: 0;
         right: 0;
         background: color(blue);
         height: 1px;
         position: absolute;
      }
   }

   .logo {
      &.is-desktop {
         display: none;

         @include media-breakpoint-up(xl) {
            display: block;
         }
      }

      &.is-mobile {
         display: none;

         @include media-breakpoint-down(xl) {
            display: block;
         }
      }

      @include media-breakpoint-down(sm) {
         width: 80px;
      }
   }

   &__rating.rating {
      @include media-breakpoint-down(lg) {
         display: none;
      }
   }

   &__burger {
      width: 80px;
      height: 88px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-left: 1px solid color(blue);
      padding-left: 30px;
      position: relative;

      @include media-breakpoint-down(sm) {
         padding-left: 15px;
      }

      @include media-breakpoint-up(lg) {
         display: none;
      }

      @include media-breakpoint-down(sm) {
         height: 60px;
         width: 60px;
      }

      &:before {
         content: '';
         width: 5px;
         height: 5px;
         background: color(blue);
         position: absolute;
         bottom: 0;
         left: 0;
         margin-left: -2.5px;
      }
   }

   &__bottom {
      display: flex;
      align-items: center;
      justify-content: space-between;

      @include media-breakpoint-down(lg) {
         display: none;
      }
   }

   &__top {
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid color(blue);

      @include media-breakpoint-up(lg) {
         padding: 15px 0;
      }
   }

   &__callback {
      @include media-breakpoint-down(md) {
         display: none;
      }
   }

   &__calc {
      font-weight: 700;
      font-size: 11px;
      border: 1px solid color(blue);
      height: 45px;
      padding: 0 15px;
      border-radius: 7px;
      text-decoration: none;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: background-color .3s, color .3s;
      color: color(blue);

      @include media-breakpoint-down(lg) {
         display: none;
      }

      svg {
         fill: color(blue);
         transition: fill .3s;
         margin-left: 10px;
      }

      &:hover {
         background-color: color(blue);
         color: white;

         svg {
            fill: white;
         }
      }
   }

   &__social {
      display: flex;
      align-items: center;

      a {
         text-decoration: none;
         margin-right: 10px;
      }
   }

   &__online {
      font-size: 11px;
      position: relative;
      font-weight: 500;
      padding-left: 13px;

      &:before {
         width: 6px;
         height: 6px;
         background: #45C655;
         position: absolute;
         left: 0;
         content: '';
         animation: online .8s alternate-reverse infinite;
         top: 50%;
         border-radius: 50%;
         margin-top: -3px;
      }
   }

   &__city {
      text-decoration: underline;
      color: color(blue);
      font-size: 11px;
      display: block;
      font-weight: 500;
      text-align: right;
      margin-top: 5px;

      &:hover {
         color: color(blue);
      }

      @include fluid-type(10px, 11px);
   }

   &__feedback {
      margin-top: 10px;
      display: flex;
      align-items: center;
   }

   &__phone {
      color: color(blue);
      font-size: 20px;
      font-weight: 600;
      text-decoration: none;
      transition: opacity .3s;

      @include fluid-type(14px, 20px);

      &:hover {
         opacity: .8;
         color: color(blue);
      }
   }

   &__menu {
      flex-grow: 1;
      max-width: 700px;
      width: 100%;
      font-size: 15px;
      position: relative;
      font-weight: 500;
      margin-right: 20px;

      @include media-breakpoint-down(xxl) {
         max-width: 600px;
      }

      @include media-breakpoint-down(xl) {
         font-size: 14px;
      }

      a {
         text-decoration: none;
         color: color(blue);
      }

      .menu-item-has-children {
         & > a {
            &:after {
               content: url("../images/icons/arrow-down.svg");
               margin-left: 8px;
            }
         }

         &:hover {
            ul {
               display: block;
            }
         }
      }

      & > ul {
         display: flex;
         align-items: center;
         @include list-null;
         width: 100%;
         justify-content: space-between;

         & > li {
            display: flex;
            align-items: center;
            min-height: 55px;
         }
      }

      ul ul {
         position: absolute;
         @include list-null;
         display: none;
         min-width: 225px;
         top: 100%;
         border-top: 3px solid color(blue);
         box-shadow: 0px -1px 10px rgba(0, 0, 0, 0.2);
         margin-top: -8px;
         background: rgba(255, 255, 255, .5);
      }

      ul ul li:not(:last-child) {
         border-bottom: 1px solid rgba(#8D8D8D, .2);
      }

      ul ul a {
         display: flex;
         font-weight: 500;
         font-size: 12px;
         line-height: 15px;
         color: #1E3859;
         padding: 8px 20px;
         transition: color .3s;

         &:hover {
            color: #8D8D8D;
         }
      }
   }

   &__badge {
      padding: 0 15px;
      font-weight: 500;
      font-size: 15px;
      line-height: 18px;
      color: #F7F7F8;
      min-height: 25px;
      display: flex;
      align-items: center;
      text-align: center;
      background-color: #6F89A2;
      justify-content: center;
      border-radius: 7px;
      transition: background-color .3s;
      text-decoration: none;

      &:hover {
         background-color: color(blue);
         color: white;
      }
   }

   &__account {
      display: flex;
      align-items: center;
      text-decoration: none;
      font-size: 15px;
      color: color(blue);

      svg {
         margin-right: 8px;

         @include media-breakpoint-down(xxl) {
            margin-right: 0;
         }
      }

      .text {
         @include media-breakpoint-down(xxl) {
            display: none;
         }
      }
   }

   &__search {
      border: 1px solid color(blue);
      border-radius: 5px;
      height: 25px;
      overflow: hidden;
      position: relative;
      max-width: 170px;
      margin: 0 20px;

      button {
         position: absolute;
         top: 0;
         left: 8px;
         background-color: transparent;
         border: none;
      }

      input {
         width: 100%;
         height: 100%;
         padding-left: 40px;
         border: none;
         font-size: 14px;

         &::placeholder {
            color: color(blue);
         }
      }
   }

   &__right {
      position: relative;
   }

   &__modal-city {
      background: #FFFFFF;
      border: 1px solid #939393;
      border-radius: 5px;
      position: absolute;
      padding: 15px 10px;
      right: 0;
      margin-top: 10px;
      white-space: nowrap;
      z-index: 99;
      display: none;

      .bottom {
         display: flex;
      }

      a {
         font-weight: 500;
         font-size: 10px;
         line-height: 12px;
         color: #FFFFFF;
         text-align: center;
         align-items: center;
         display: flex;
         height: 25px;
         flex-grow: 1;
         text-decoration: none;
         justify-content: center;
         border-radius: 3px;
         transition: .3s;

         &:first-child {
            margin-right: 10px;
            background: #C3C3C3;

            &:hover {
               background: #1E3859;
            }
         }

         &:last-child {
            border: 1px solid #8D8D8D;
            color: #000000;

            &:hover {
               background: #1E3859;
               color: white;
               border-color: #8D8D8D;
            }
         }
      }

      p {
         font-weight: 500;
         font-size: 12px;
         line-height: 15px;
         color: #1E3859;
         margin-bottom: 10px;
      }
   }
}

@keyframes online {
   100% {
      background-color: darken(#45C655, 20%);
      transform: scale(1.2);
   }
}

.site-header-short {
   position: fixed;
   top: 0;
   left: 0;
   right: 0;
   padding: 20px 0;
   background: rgba(255, 255, 255, 0.7);
   box-shadow: 0px -1px 10px rgba(0, 0, 0, 0.2);
   backdrop-filter: blur(5px);
   transform: translateY(-100%);
   transition: transform .1s;
   z-index: 800;

   @include media-breakpoint-down(xl) {
      display: none;
   }

   &.fixed {
      transform: none;
   }

   &__row {
      display: flex;
      justify-content: space-between;
      align-items: center;
   }

   .site-header__menu {
      padding: 0 40px;
      max-width: 700px;
   }

   .site-header__search {
      max-width: 100px;
   }

   @include media-breakpoint-down(xxl) {
      .site-header__badge {
         display: none;
      }
   }
}

.site-mobile-bar {
   position: fixed;
   right: 0;
   top: 0;
   bottom: 0;
   background: rgba(255, 255, 255, 0.85);
   backdrop-filter: blur(10px);
   border-radius: 0;
   z-index: 90000;
   overflow-y: scroll;
   transform: translateX(100%);
   width: 300px;

   svg {
      stroke: color(blue);
   }

   &.active {
      transform: none;
      box-shadow: 0px -1px 10px rgba(0, 0, 0, 0.2);
   }

   & > div {
      border-bottom: 1px solid #8D8D8D;
      padding: 25px 30px;
      text-align: right;

      @include media-breakpoint-down(md) {
         padding: 15px 20px;
      }

      button {
         background: transparent;
         padding: 0;
         border: none
      }
   }

   ul {
      @include list-null;

      ul {
         margin-top: 20px;
         padding-left: 20px;

         li:not(:last-child) {
            margin-bottom: 15px;
         }

         a {
            font-weight: 500;
            font-size: 18px;
            line-height: 22px;
            color: #1E3859;

            @include media-breakpoint-down(md) {
               font-size: 16px;
            }
         }
      }
   }

   a {
      text-decoration: none;
   }

   & > ul > li {
      padding: 25px 30px;

      @include media-breakpoint-down(md) {
         padding: 15px 20px;
      }

      &:not(:last-child) {
         border-bottom: 1px solid #8D8D8D;
      }

      & > a {
         font-weight: 600;
         font-size: 20px;
         line-height: 24px;
         color: #1E3859;

         @include media-breakpoint-down(md) {
            font-size: 18px;
         }
      }
   }
}
