.rating {
   font-size: 11px;
   color: color(blue);
   display: flex;
   align-items: center;
   font-weight: 500;

   &__star {
      position: relative;
      width: 50px;
      height: 50px;
      display: flex;
      font-weight: 700;
      align-items: center;
      justify-content: center;
      font-size: 12px;
      margin-right: 15px;

      span {
         position: absolute;
      }
   }
}
