.form-input-solid {
   border: none;
   border-bottom: 1px solid #FFFFFF;
   width: 100%;
   font-weight: 400;
   font-size: 18px;
   line-height: 22px;
   color: #FFFFFF;
   background: transparent;
   height: 40px;

   @include media-breakpoint-down(md) {
      font-size: 14px;
   }

   &::placeholder {
      color: white;
   }
}

.form-input {
   background: #F7F7F8;
   border-radius: 7px;
   height: 55px;
   padding-left: 15px;
   font-weight: 400;
   font-size: 20px;
   line-height: 24px;
   width: 100%;
   border: none;

   @include media-breakpoint-down(md) {
      font-size: 16px;
      height: 45px;
   }

   &::placeholder {
      color: #000000;
   }
}
