.project {
   box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
   border-radius: 0px;
   background: white;

   @include media-breakpoint-up(sm) {
      @include media-breakpoint-down(xl) {
         display: flex;
      }
   }

   @include media-breakpoint-down(lg) {
      .btn-green {
         margin-bottom: 10px;
      }
   }

   &-title {
      font-weight: 700;
      color: color(dark);
      transition: .3s;
      text-decoration: none;

      @include fluid-type(24px, 32px);

      &:hover {
         color: color(blue);
      }
   }

   &-thumb {
      height: 280px;
      display: block;

      @include media-breakpoint-up(sm) {
         @include media-breakpoint-down(xl) {
            height: initial;
            max-height: 280px;
            flex: 0 0 50%;
            max-width: 50%;
         }
      }

      @include media-breakpoint-down(sm) {
         height: 220px;
      }

      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
   }

   &-price {
      color: #43638A;
      font-weight: 700;
      margin: 5px 0 20px;

      @include fluid-type(20px, 30px);

      span {
         font-weight: 400;
         color: #1F2221;

         @include fluid-type(14px, 20px);
      }
   }

   &-content {
      padding: 20px 30px;
      flex-grow: 1;

      @include media-breakpoint-down(lg) {
         padding: 15px;
      }

      @include media-breakpoint-up(sm) {
         @include media-breakpoint-down(xl) {
            display: flex;
            justify-content: center;
            flex-direction: column;
         }
      }
   }
}

.project-full {
   background: #FFFFFF;
   box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);

   &__save {
      width: 45px;
      height: 30px;
      display: flex;
      align-items: center;
      z-index: 2;
      position: absolute;
      bottom: 15px;
      right: 30px;
      background-color: red;
      justify-content: center;
      background-color: color(blue);
   }

   @include media-breakpoint-down(lg) {
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
   }

   &__location {
      padding: 20px;
      font-weight: 600;
      font-size: 20px;
      line-height: 24px;
      color: #000000;
      display: flex;
      align-items: center;

      @include media-breakpoint-down(sm) {
         padding: 10px;
      }

      svg {
         width: 30px;
         height: 36px;
         margin-right: 15px;
      }
   }

   &-thumb {
      height: 250px;
      display: block;
      overflow: hidden;
      position: relative;

      &:hover {
         img {
            transform: scale(1.1);
         }
      }

      @include media-breakpoint-down(sm) {
         height: 200px;
      }

      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
         transition: transform .3s;
      }
   }

   &-title {
      font-weight: 700;
      color: #1F2221;
      text-decoration: none;
      margin-bottom: 20px;
      display: block;
      line-height: 120%;

      @include media-breakpoint-down(md) {
         margin-bottom: 10px;
      }

      @include fluid-type(18px, 25px);

      &:hover {
         color: color(blue);
      }
   }

   &-props {
      margin-top: 30px;
   }

   &-prop {
      border-bottom: 1px solid rgba(141, 141, 141, 0.4);
      padding-bottom: 8px;
      margin-bottom: 8px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      span {
         &:first-child {
            font-weight: 400;
            font-size: 16px;
            line-height: 20px;
            display: flex;

            @include fluid-type(14px, 16px);
         }

         &:last-child {
            font-weight: 400;
            font-size: 20px;
            line-height: 24px;

            @include fluid-type(16px, 20px);
         }
      }

      img {
         margin-right: 15px;
      }
   }

   &-price {
      font-size: 30px;
      line-height: 24px;
      font-weight: 700;
      @include fluid-type(20px, 30px);

      @include media-breakpoint-down(xxl) {
         margin-bottom: 10px;
      }

      span {
         font-weight: 400;

         @include fluid-type(14px, 20px);
      }
   }

   &-bottom {
      margin-top: 30px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      @include media-breakpoint-down(xxl) {
         flex-direction: column;
      }
   }

   &-content {
      padding: 20px;

      @include media-breakpoint-down(sm) {
         padding: 10px;
      }
   }
}
