.home-projects {
   padding: 70px 0 90px;

   @include media-breakpoint-down(lg) {
      padding-bottom: 50px;

   }
}

.home-projects-full {
   &:not(.is-page) {
      @include media-breakpoint-up(md) {
         background: white;
         padding-top: 50px;
         padding-bottom: 30px;
      }
   }

   .slick-arrow {
      position: absolute;
      z-index: 8;
     top: 140px!important;
   }

   .slick-next {
      right: 20px;
   }

   .slick-prev {
      left: 20px;
   }

   .slick-list {
      margin: -30px -10px;
   }

   .slick-slide {
      padding: 30px 10px;
   }

   .grid {
      display: none;

      @include media-breakpoint-up(lg) {
         display: flex;
      }
   }

   .slider {
      @include media-breakpoint-up(lg) {
         display: none;
      }
   }

   &__bottom {
      margin-top: 50px;
      text-align: center;

      @include media-breakpoint-down(md) {
         display: none;
      }

      .btn {
         min-width: 270px;
      }
   }
}

.home-feedback {
   padding-top: 115px;

   @include media-breakpoint-down(md) {
      padding-top: 50px;
   }

   .btn {
      @include fluid-type(16px, 22px);
   }
}

.home-work {
   padding: 115px 0 90px;
   overflow: hidden;

   @include media-breakpoint-down(md) {
      padding: 50px 0;
   }

   .grid {
      display: none;

      @include media-breakpoint-up(md) {
         display: flex;
      }
   }

   .slider {
      @include media-breakpoint-up(md) {
         display: none;
      }
   }

   &__row.row {
      margin: 0 -40px -30px;

      @include media-breakpoint-down(xl) {
         margin-left: -15px;
         margin-right: -15px;
      }

      & > * {
         padding: 0 40px 30px;

         @include media-breakpoint-down(xl) {
            padding-left: 15px;
            padding-right: 15px;
         }
      }
   }
}

.center-slider {
   text-align: center;

   @include media-breakpoint-up(xxl) {
      .slick-slider {
         max-width: 1300px;
         margin: 0 auto;
      }

      .slick-next.slick-arrow {
         right: -70px;
      }

      .slick-prev.slick-arrow {
         left: -70px;
      }
   }

   img {
      margin: 0 auto;
   }
}

.home-reviews {
   padding-bottom: 90px;

   @include media-breakpoint-down(md) {
      padding-bottom: 50px;
   }
}

.home-question {
   padding-bottom: 115px;

   @include media-breakpoint-down(md) {
      padding-bottom: 50px;
   }

   &__link {
      margin-bottom: 70px;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
      color: #43638A;
      background-color: transparent;
      border: none;
      padding: 0;

      @include media-breakpoint-up(lg) {
         display: none;
      }

      @include media-breakpoint-down(md) {
         margin-bottom: 30px;
      }
   }

   &__text {
      font-weight: 400;
      font-size: 22px;
      line-height: 1.3;
      position: relative;

      p {
         margin: 0;

         &:not(:first-child) {
            @include media-breakpoint-down(lg) {
               display: none;
            }
         }
      }

      &:not(.show) {
         @include media-breakpoint-down(lg) {
            &:before {
               background: linear-gradient(360deg, #F7F7F8 0%, rgba(247, 247, 248, 0) 100%);
               position: absolute;
               left: 0;
               right: 0;
               content: '';
               height: 100px;
               bottom: 0;
            }
         }
      }

      &.show {
         p {
            display: block!important;
         }
      }

      @include fluid-type(14px, 22px);
   }

   &__row {
      display: flex;

      @include media-breakpoint-down(lg) {
         display: block;
      }

      &.is-big {
         @include media-breakpoint-down(xxl) {
            display: block;
         }
      }
   }

   &__left {
      flex-grow: 1;

      &.is-big {
         @include media-breakpoint-up(xxl) {
            padding-right: 70px;
         }
      }

      &:not(.is-big) {
         @include media-breakpoint-up(lg) {
            padding-right: 70px;
         }
      }
   }

   &__right {
      &:not(.is-big) {
         @include media-breakpoint-up(lg) {
            max-width: 430px;
            flex: 0 0 430px;
         }

        display: none;
      }

      &.is-big {
         @include media-breakpoint-up(xxl) {
            max-width: 430px;
            flex: 0 0 430px;
         }

         @include media-breakpoint-down(xxl) {
            .consultation{
               display: none;
            }
         }
      }
   }

   &__feedback {
      margin-top: 40px;

      @include media-breakpoint-up(lg) {
         &:not(.is-big) {
            display: none;
         }
      }
      
      &.is-big {
         @include media-breakpoint-up(xxl) {
            display: none;
         }
      }
   }
}

.home-contacts {
   margin-top: 115px;
   padding-top: 70px;
   padding-bottom: 50px;
   border-top: 1px solid #8D8D8D;
   position: relative;

   @include media-breakpoint-down(md) {
      padding-top: 50px;
      margin-top: 0;
      border: none;
   }

   &__wrapper {
      position: relative;
   }

   &__mobile {
      background: #43638A;
      padding: 20px 0;
      color: white;
      font-size: 14px;

      @include media-breakpoint-up(lg) {
         display: none;
      }

      .item {
         display: flex;
         align-items: center;
         justify-content: center;

         svg {
            margin-right: 10px;
         }
      }

      .items {
         display: flex;
         justify-content: center;
         margin-top: 10px;

         & > .item:first-child {
            margin-right: 15px;
         }
      }
   }

   &__map {
      height: 100%;

      @include media-breakpoint-up(lg) {
         margin-right: 20px;
         position: relative;
      }

      @include media-breakpoint-down(lg) {
         min-height: 370px;
      }
   }

   &__list {
      background: #FFFFFF;
      padding: 30px;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      min-width: 400px;
      font-weight: 400;

      @include media-breakpoint-down(md) {
         display: none;
      }

      @include media-breakpoint-down(xl) {
         margin: 0;
      }

      ul {
         @include list-null;
         line-height: 1.5;

         a {
            font-weight: 400;
            font-size: 16px;
            line-height: 20px;
            color: #1F2221;
            text-decoration: none;

            &:hover {
               color: color(blue);
            }
         }

         li {
            display: flex;
            align-items: center;

            svg {
               margin-right: 18px;
            }

            &:not(:last-child) {
               margin-bottom: 20px;
            }
         }
      }

      &-title {
         font-weight: 700;
         font-size: 27px;
         line-height: 33px;
         margin-bottom: 20px;
      }
   }

   iframe {
      position: absolute;
      right: 0;
      top: 0;
      bottom: 0;
      height: 100%;
      width: 2000px;
   }
}

.slick-arrow {
   background: transparent;
   padding: 0;
   border: none;
   position: absolute;
   z-index: 2;
   width: 45px;
   height: 45px;

   &.slick-prev {
      transform: rotate(-180deg);
   }

   &.slick-arrow-center {
      top: 50%;
      margin-top: -28.5px;
   }

   &.slick-prev {
      left: 15px;
   }

   &.slick-next {
      right: 15px;
   }
}

.home-certificates {
   @include media-breakpoint-up(xxl) {
      max-width: 1200px;
      margin: 0 auto;

      .slick-prev {
         left: -110px;
      }

      .slick-next {
         right: -110px;
      }
   }
}
