.filter {
   margin-bottom: -3px;
   position: relative;
   z-index: 3;

  &-sorting {
    border: 1px solid #8D8D8D;
    border-radius: 5px;
    padding: 10px;
    height: 100%;

    & > div:first-child {
      margin-bottom: 10px;
    }

    svg {
      stroke: #8D8D8D;
      fill: none;
    }
    
    label {
      cursor: pointer;
    }

    input {
      display: none;
      
      &:checked + label {
        svg {
          stroke: color(blue);
        }
      }
    }

    .check {
      display: flex;
      cursor: pointer;
      justify-content: space-between;

      &:first-child {
        margin-right: 10px;
      }
    }

    .checks {
      display: flex;
    }

    & > div:last-child {
      font-size: 14px;

      & > div {
        display: flex;
        justify-content: space-between;

        &:first-child {
          margin-bottom: 5px;
        }
      }
    }
  }

   &-clear {
      @include media-breakpoint-up(sm) {
         height: 35px;
      }
   }

   &-group {
      border-radius: 7px;

      @include media-breakpoint-down(lg) {
         margin-bottom: 25px;
      }

      @include media-breakpoint-up(md) {
         display: flex;
         box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
      }

      .dropdown-select {
         flex-grow: 1;

         @include media-breakpoint-down(md) {
            &:not(:last-child) {
               margin-bottom: 15px;
            }
         }

         @include media-breakpoint-up(md) {
            &:first-child {
               .dropdown-select__top {
                  border-radius: 7px 0 0 7px;
               }
            }

            &:last-child {
               .dropdown-select__top {
                  border-radius: 0 7px 7px 0;
               }
            }

            &:not(:last-child) {
               .dropdown-select__top {
                  border-right: none;
               }
            }
         }

         &__top {
            height: 45px;
            border-radius: 0;

            @include media-breakpoint-down(md) {
               border-radius: 7px;
            }
         }
      }
   }

   @include media-breakpoint-down(lg) {
      margin: 0 -15px;
      background: white;
      padding: 15px 15px 30px 15px;
      margin-bottom: 70px;
   }

   @include media-breakpoint-down(md) {
      margin-bottom: 50px;
   }

   &-mobile-text {
      margin-bottom: 10px;

      @include media-breakpoint-up(lg) {
         display: none;
      }
   }

   &-title {
      font-weight: 600;
      line-height: 34px;
      align-items: flex-end;
      @include fluid-type(20px, 28px);

      @include media-breakpoint-up(lg) {
         background: #6F89A2;
         border-radius: 7px 7px 0px 0px;
         display: inline-flex;
         width: 360px;
         height: 45px;
         color: #FFFFFF;
         padding-left: 20px;
      }
   }

   &-right {
      height: 100%;

      @include media-breakpoint-up(xl) {
         padding-left: 30px;
         border-left: 1px solid #8D8D8D;
      }

      @include media-breakpoint-down(xl) {
         margin-bottom: 30px;

         @include media-breakpoint-up(lg) {
            margin-top: 20px;
         }
      }
   }

   &-content {
      @include media-breakpoint-up(lg) {
         background: #6F89A2;
         border-radius: 0px 7px 0px 0px;
         padding: 20px;
      }
   }

   &-wrapper {
      @include media-breakpoint-up(lg) {
         padding: 30px 40px;
         background: #FFFFFF;
         border-radius: 5px;
      }

      & > .row {
         align-items: center;

         @include media-breakpoint-down(lg) {
            flex-direction: column-reverse;
         }
      }
   }

   &-box {
      border: 1px solid #8D8D8D;
      border-radius: 5px;
      padding: 10px 13px 0 13px;

      &__form {
         margin: 12px 0 17px;
         background: #F2F2F3;
         display: flex;
         align-items: center;
         height: 30px;

         input {
            height: 100%;
            width: 50%;
            border: none;
            background: transparent;
            border-radius: 0;
            text-align: center;
            font-weight: 400;
            font-size: 14px;
            line-height: 17px;
            color: #8D8D8D;

            &:focus {
               outline: none;
            }
         }
      }
   }

   &-clear {
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
      color: #1F2221;
      transition: color .3s;
      text-decoration: none;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;

      svg {
         margin-right: 9px;
      }

      &:hover {
         color: color(blue);
      }
   }

   &-search {
      width: 100%;
      position: relative;
      border: 1px solid #8D8D8D;
      border-radius: 5px;
      height: 36px;
      overflow: hidden;
      margin-top: 10px;

      svg {
         position: absolute;
         left: 10px;
         top: 50%;
         margin-top: -7px;
      }

      input {
         width: 100%;
         height: 100%;
         border: none;
         border-radius: 5px;
         padding: 0;
         font-weight: 400;
         font-size: 14px;
         line-height: 17px;
         color: #8D8D8D;
         padding-left: 40px;
      }

      button {
         position: absolute;
         right: -2px;
         border-radius: 0 5px 5px 0;
      }
   }
}
