.breadcrumb {
  display: flex;
  @include list-null;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;

  @include media-breakpoint-down(md) {
    li {
      display: none;

      &:last-child, &:first-child {
        display: block;
      }
    }
  }

  a {
    color: color(blue);
    text-decoration: none;
  }

  span {
    color: color(blue);
    text-decoration: underline;
  }

  a:after {
    content: '/';
    margin: 0 10px;
  }
}
