.jumbotron {
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: cover;
  max-width: 2500px;
  margin: -145px auto 0;
  position: relative;
  overflow: hidden;

  &__button-mobile {
    width: 85px;
    height: 85px;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    background: #1E3859;
    font-size: 10px;
    border-radius: 50%;
    line-height: 12px;
    text-align: center;
    color: #FFFFFF;
    text-decoration: none;
    left: 30px;
    top: 20px;
    transition: background-color .3s, color .3s;

    &:hover {
      background-color: white;
      color: #1E3859;

      &:after {
        background-color: #1E3859;
      }
    }

    &:after {
      width: 33px;
      height: 33px;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: white;
      content: url("../images/icons/arrow-right.svg");
      position: absolute;
      top: -7px;
      transition: background-color .3s;
      right: -7px;
      border-radius: 50%;
    }
    
    @include media-breakpoint-up(md) {
      display: none;
    }
  }

  &__image {
    margin: 0 -30px;
    position: relative;

    @include media-breakpoint-down(sm) {
      margin: 0 -15px;
    }

    img {
      max-width: 100%;
      min-height: 300px;
      object-fit: cover;
    }

    @include media-breakpoint-up(md) {
      display: none;
    }
  }

  @include media-breakpoint-down(md) {
    margin: 0;
    background-image: none !important;
  }

  &:before {
    @include media-breakpoint-up(md) {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.8) -4.75%, rgba(0, 0, 0, 0) 9.5%), linear-gradient(180deg, rgba(255, 255, 255, 0.9) 19.56%, rgba(225, 225, 225, 0.317045) 31.92%, rgba(209, 209, 209, 0) 88.96%);
    }
  }

  &__bottom {
    @include media-breakpoint-up(md) {
      display: flex;
      justify-content: space-between;
      margin: 0 -20px -20px;
      flex-wrap: wrap;
    }

    @include media-breakpoint-down(md) {
      background: color(blue);
      padding: 15px;
      margin-top: -20px;
      position: relative;
      z-index: 4;
    }

    & > div {
      @include media-breakpoint-up(md) {
        max-width: 22%;
        flex: 0 0 22%;
        padding: 0 20px 20px;

        @include media-breakpoint-down(xl) {
          flex: 0 0 50%;
          max-width: 50%;
        }
      }
      @include media-breakpoint-down(md) {
        &:not(:last-child) {
          margin-bottom: 15px;
        }
      }
    }
  }

  h1 {
    font-weight: 800;
    margin: 0;
    line-height: 1.5;
    color: #F7F7F8;
    text-shadow: 0 2px 25px #000000;

    @include fluid-type(20px, 51px);

    @include media-breakpoint-down(xl) {
      text-shadow: 0 2px 10px #000000;
    }
  }

  .container {
    height: 100%;
    position: relative;
    z-index: 2;
  }

  &__content {
    @include media-breakpoint-up(md) {
      height: 750px;
      padding: 260px 0 20px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      @include media-breakpoint-down(xl) {
        height: 910px;
        padding: 180px 0 20px;
      }
    }
  }

  &__top {
    @include media-breakpoint-up(xl) {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }

  &__head {
    @include media-breakpoint-down(md) {
      background: color(blue);
      padding: 20px 30px;
      margin: 0 -30px;
    }

    @include media-breakpoint-down(sm) {
      margin: 0 -15px;
      padding: 20px 15px;
    }

    p {
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      color: #F7F7F8;
      margin: 4px 0 0;

      @include fluid-type(14px, 20px);

      @include media-breakpoint-up(md) {
        display: none;
      }
    }
  }

  &__action {
    @include media-breakpoint-up(md) {
      background: rgba(62, 62, 62, 0.5);
      border: 1px solid rgba(141, 141, 141, 0.5);
      backdrop-filter: blur(1.5px);
      border-radius: 9px;
      font-weight: 700;
      color: #F7F7F8;
      display: flex;
      min-height: 75px;
      padding: 0 20px;
      align-items: center;
      max-height: 75px;
    }

    @include media-breakpoint-down(md) {
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      color: #F7F7F8;
      padding-left: 20px;
      position: relative;

      &:before {
        content: '';
        width: 5px;
        height: 5px;
        background: white;
        position: absolute;
        left: 0;
        top: 50%;
        margin-top: -2.5px;
      }

      br {
        display: none;
      }
    }

    div {
      margin-right: 18px;
      display: none;

      @include media-breakpoint-up(md) {
        display: block;
      }

      @include media-breakpoint-down(xl) {
        width: 120px;
        text-align: center;
      }
    }
  }

  &__feedback {
    max-width: 480px;
    flex: 0 0 480px;
    background: rgba(62, 62, 62, 0.5);
    border: 1px solid #8D8D8D;
    backdrop-filter: blur(1.5px);
    border-radius: 10px;
    padding: 15px 25px;
    color: #F7F7F8;
    text-align: center;

    div {
      margin: 20px 0 15px;
    }

    @include media-breakpoint-down(md) {
      display: none;
    }

    @include media-breakpoint-down(xl) {
      width: 380px;
      margin-left: -30px;
      border-radius: 0 10px 10px 0;
      margin-top: 60px;
    }

    @include media-breakpoint-down(lg) {
      margin-left: -31px;
    }

    &-title {
      font-weight: 700;
      font-size: 20px;
      line-height: 24px;
    }

    input {
      width: 100%;
      border-radius: 8px;
      border: 1px solid #F7F7F8;
      text-align: center;
      height: 35px;
      background: transparent;
      color: white;
      font-size: 20px;

      &:focus {
        outline: none;
      }

      &::placeholder {
        color: white;
      }
    }

    &-text {
      font-weight: 400;
      font-size: 15px;
    }
  }
}
