.site-footer {
   background: #FFFFFF;

   .logo {
      &.is-desktop {
         display: none;

         @include media-breakpoint-up(xl) {
            display: block;
         }
      }

      &.is-mobile {
         display: none;

         @include media-breakpoint-down(xl) {
            display: block;
         }
      }
   }

   &__top {
      @include media-breakpoint-down(lg) {
         display: none;
      }
   }

   &__title {
      margin-bottom: 10px;
      font-weight: 700;
      font-size: 15px;
      line-height: 18px;
      color: #1F2221;
   }

   &__list {
      @include list-null;

      li {
         &:not(:last-child) {
            margin-bottom: 5px;
         }
      }

      a {
         text-decoration: none;
         font-weight: 400;
         font-size: 15px;
         line-height: 18px;
         color: #B3B3B3;

         &:hover {
            color: color(blue);
         }
      }
   }

   &__socials {
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
      width: 100%;

      a:not(:last-child) {
         margin-right: 10px;
      }
   }

   &__menu {
      border-bottom: 1px solid #000000;
      padding-bottom: 35px;
      margin-bottom: 35px;

      ul {
         @include list-null;
         display: flex;

         li {
            &:not(:last-child) {
               margin-right: 40px;
            }
         }

         a {
            font-weight: 700;
            font-size: 15px;
            line-height: 18px;
            color: #1F2221;
            text-decoration: none;

            &:hover {
               color: color(blue);
            }
         }
      }
   }

   &__contacts {
      display: flex;
      flex-wrap: wrap;

      a {
         color: white;
         text-decoration: none;
         transition: opacity .3s;

         &:hover {
            opacity: .5;
         }
      }
   }

   &__top {
      padding: 50px 0;
   }

   &__contacts {
      display: flex;

      & > *:first-child {
         margin-right: 15px;
      }
   }

   &__desktop {
      @include media-breakpoint-down(lg) {
         display: none;
      }
   }

   &__mobile {
      @include media-breakpoint-down(sm) {
         display: none;
      }
   }

   &__span {
      margin-bottom: 7px;
      display: block;
   }

   &__bottom {
      background: #2B2E31;
      padding: 50px 0;
      color: #FFFFFF;

      @include media-breakpoint-down(md) {
         padding: 20px 0;
      }

      .row {
         align-items: center;
      }
   }
}
