.feedback {
  padding: 60px 0;
  color: white;
  position: relative;
  background: #43638A;

  & .container {
    & > .row {
      justify-content: space-between;
    }
  }

  @include media-breakpoint-up(lg) {
    background: url("../images/bgs/feedback.jpg") no-repeat center/cover;
  }

  @include media-breakpoint-down(lg) {
    padding: 30px 0;
  }

  &__list {
    margin: 0;
    padding-left: 20px;
    list-style-type: square;
    margin-bottom: 20px;

    @include media-breakpoint-up(lg) {
      display: none;
    }

    li:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  &__col {
    position: relative;
    z-index: 4;
  }

  @include media-breakpoint-up(lg) {
    &:before {
      content: '';
      background: rgba(67, 99, 138, 0.8);
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
    }
  }

  &__button {
    @include media-breakpoint-down(md) {
      display: none;
    }
  }

  &__image {
    position: absolute;
    right: 0;
    display: none;

    @include media-breakpoint-up(lg) {
      top: 0;
    }

    @include media-breakpoint-up(md) {
      display: block;
    }

    @include media-breakpoint-down(lg) {
      width: 50%;
      bottom: -14%;
    }
  }

  .h2 {
    margin-bottom: 5px;

    @include media-breakpoint-up(lg) {
      margin-bottom: 15px;
    }
  }

  &__head {
    margin-bottom: 80px;

    @include media-breakpoint-down(lg) {
      margin-bottom: 30px;
    }
  }

  &__button-mobile {
    position: absolute;
    right: 10px;
    top: 70%;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: white;
    border: none;

    @include media-breakpoint-up(md) {
      display: none;
    }
  }

  form {
    justify-content: flex-end;

    @include media-breakpoint-up(lg) {
      margin-left: -30px;
      margin-right: -30px;

      & > div {
        padding-left: 30px;
        padding-right: 30px;
      }
    }

    @include media-breakpoint-down(lg) {
      margin-bottom: -30px;

      & > div {
        padding-bottom: 30px;
      }
    }

    @include media-breakpoint-down(md) {
      margin-bottom: -15px;

      & > div {
        padding-bottom: 15px;
      }
    }
  }

  .btn {
    margin-top: 30px;
  }

  &__steps {
    position: relative;
    z-index: 22;

    @include media-breakpoint-down(lg) {
      display: none;
    }

    & > div {
      background: #41556C;
      border-radius: 10px;
      max-width: 430px;
      margin-left: auto;
      height: 75px;
      font-weight: 400;
      font-size: 20px;
      line-height: 24px;
      color: #FFFFFF;
      display: flex;
      align-items: center;
      padding-left: 60px;
      position: relative;
      padding-right: 15px;


      svg {
        position: absolute;
        left: 15px;
        top: 50%;
        margin-top: -6px;
      }

      &:not(:last-child) {
        margin-bottom: 22px;
      }
    }
  }
}
