.feedback {
   &-container {
      background: #1E3859;
   }
}

.feedback-user {
   display: flex;
   justify-content: space-between;
   position: relative;
   overflow: hidden;
   align-items: center;

   @include media-breakpoint-down(lg) {
      margin-left: -30px;
      margin-right: -30px;
   }

   @include media-breakpoint-down(sm) {
      margin-left: -15px;
      margin-right: -15px;
   }

   @include media-breakpoint-down(sm) {
      background: #43638A;
   }

   .btn {
      @include fluid-type(16px, 22px);
   }

   .form {
      margin: 50px auto 0;
      text-align: center;

      @include media-breakpoint-up(md) {
         max-width: 470px;
      }

      @include media-breakpoint-down(sm) {
         margin: 25px 0 0;
      }

      & > div {
         margin-bottom: 20px;
      }
   }

   .title {
      @include fluid-type(14px, 54px);

      @include media-breakpoint-up(md) {
         max-width: 725px;
         margin: 0 auto;
         text-align: center;
      }
   }

   .content {
      flex-grow: 1;
      padding: 30px 30px;
      color: white;

      @include media-breakpoint-down(sm) {
         padding: 20px 15px;
      }
   }

   .img {
      max-width: 380px;
      flex: 0 0 380px;

      @include media-breakpoint-down(sm) {
         display: none;
      }

      img {
         width: 100%;
         height: 480px;
         object-fit: cover;
      }
   }

   .btn {
      @include media-breakpoint-up(md) {
         max-width: 300px;
         margin-top: 30px;
      }
   }
}
