.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  transition: .3s;
  text-decoration: none;
  border: none;
  height: 50px;

  @include media-breakpoint-down(md) {
    height: 45px;
  }
}

.btn-border {
  background-color: transparent;
  border: 1px solid color(blue);
  color: color(blue);

  &:hover {
    background-color: color(blue);
    color: white;
  }
}

.btn-blue-white {
  background-color: color(blue);
  color: white;

  &:hover {
    background-color: #F7F7F8;
    color: #000;
  }
}

.btn-blue {
  background: #43638A;
  border-radius: 5px;
  color: white;

  &:hover {
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.3) 100%), #43638A;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 5px;
    color: white;
  }
}

.btn-green {
  background: #88B36F;
  border-radius: 5px;
  color: white;

  &:hover {
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.3) 100%), #88B36F;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 5px;
    color: white;
  }
}

.btn-default {
  background: #F7F7F8;
  border: 1px solid #D1D1D1;
  border-radius: 7px;
  font-weight: 500;
  font-size: 16px;
  color: #1F2221;

  &:hover {
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.3) 100%), #F7F7F8;
    border: 1px solid #D1D1D1;
    border-radius: 7px;
    color: #1F2221;
  }
}

.btn-xs {
  height: 28px;
  padding: 0 15px;
  border-radius: 3px;
  font-size: 11px;
}

.btn-sm {
  height: 35px !important;
  padding: 0 30px;
  border-radius: 5px;
  font-size: 13px;
}

.btn-md {
  height: 40px;
  padding: 0 24px;
  border-radius: 5px;
  font-size: 16px;
}

.btn-block {
  width: 100%;
}

.btn-lg {
  height: 60px;
  font-size: 22px;
  line-height: 27px;

  @include fluid-type(16px, 22px);

  @include media-breakpoint-down(md) {
    height: 45px;
  }
}
