.work {
   background: #FFFFFF;
   border-radius: 7px;
   position: relative;
   padding: 40px 40px 0;

   @include media-breakpoint-down(md) {
      padding: 15px 15px 15px 40px;
   }

   @include media-breakpoint-up(md) {
      height: 420px;
      border-radius: 7px;
   }

   &__image {
      text-align: right;
      margin-bottom: 10px;

      img {
         width: 55px;
      }
   }

   &__number {
      background: #43638A;
      border-radius: 7px 0px;
      width: 76px;
      height: 57px;
      position: absolute;
      top: 0;
      left: 0;
      color: #FFFFFF;
      font-size: 26px;
      display: flex;
      align-items: center;
      justify-content: center;

      @include media-breakpoint-down(md) {
         height: 100%;
         width: 30px;
         font-weight: 500;
         font-size: 18px;
         line-height: 22px;
         color: #FFFFFF;
         align-items: flex-start;
         padding-top: 10px;
      }
   }

   &__title {
      font-weight: 700;
      font-size: 22px;
      line-height: 1.2;
      margin-bottom: 10px;

      @include fluid-type(16px, 22px);
   }

   ul {
      font-weight: 400;
      font-size: 19px;
      @include list-null;

      @include fluid-type(16px, 19px);

      li {
         margin-bottom: 2px;
         padding-left: 25px;
         position: relative;

         &:before {
            content: url("../images/icons/check.svg");
            position: absolute;
            top: 50%;
            margin-top: -9px;
            left: 0;
         }
      }
   }

   &:before {
      content: '';
      position: absolute;
      left: 15px;
      right: 15px;
      bottom: 15px;
      top: 15px;
      border: 1px solid color(blue);

      @include media-breakpoint-down(md) {
         left: 0;
         right: 0;
         bottom: 0;
         top: 0;
         border-radius: 7px;
      }
   }
}
